.home {
  width: 100vw;
  height: 100vh;
}

.about {
  margin-bottom: 60px;
  /* width: 100vw;
  height: 100vh; */
}

#profilePic {
  width: 60%;
  border-radius: 100%;
  margin: 0 auto;
}

.introText {
  margin-top: 5%;
}

h1 {
  grid-row: 1;
  grid-column: 1;
  top: 560px;
  width: 100%;
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.5s;
  font-size: 55px !important;
  text-align: left;
  font-family: "Rambla", sans-serif !important;
  padding: 50px 50px 0 50px !important;
  color: white;
  text-shadow: 0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black,
    0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black,
    0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black,
    0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black, 0 0 4px black,
    0 0 4px black;
  letter-spacing: 5px;
  font-family: "Bungee Shade", cursive !important;
  margin-top: 45px !important;
  /* font-family: "Elsie Swash Caps", cursive !important; */
}

.subTitle {
  font-size: 50px !important;
  line-height: initial;
  padding-left: 50px;
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.5s;
}

#aboutBlurb {
  margin: 15% 10% 0 0;
  /* width: 80%; */
  font-size: 18px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.button-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.aboutBtns {
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.aboutBtn {
  color: black;
  background: none;
  border-radius: 0;
  padding: 1.2em 5em;
  letter-spacing: 0.3em;
  font-size: 0.7em;
  transition: background-color 0.3s, box-shadow 0.3s, color 0.3s;
  margin: 1em;

  box-shadow: inset 0 0 1em rgba(0, 170, 170, 0.5),
    0 0 1em rgba(0, 170, 170, 0.5);
  border: #0dd solid 2px;

  box-shadow: inset 0 0 0.5em rgba(0, 0, 0, 0.25), 0 0 0.5em rgba(0, 0, 0, 0.25);
  border: #000 solid 2px;
  font-weight: bold !important;
}

.aboutBtn:hover {
  background-color: #0dd;
  box-shadow: inset 0 0 0 rgba(0, 170, 170, 0.5),
    0 0 1.5em rgba(0, 170, 170, 0.7);
  cursor: pointer;
  color: white;

  background-color: #000;
  box-shadow: inset 0 0 0 rgba(0, 0, 0, 0.5), 0 0 1.5em rgba(0, 0, 0, 0.7);
}

.glass-btn {
  width: 100px;
  height: 100px;
  background-color: #fafafa;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 7px;
  margin-left: 1em;
  margin-right: 1em;
  cursor: pointer;
  transition: all 0.3s;
  margin: 20px;
}

.blue-btn {
  -webkit-box-shadow: 0px 20px 20px -17px rgba(0, 133, 244, 0.53);
  -moz-box-shadow: 0px 20px 20px -17px rgba(0, 133, 244, 0.53);
  box-shadow: 0px 20px 20px -17px rgba(0, 133, 244, 0.53);
}

.blue-btn:hover {
  -webkit-box-shadow: 0px 20px 35px -16px rgba(0, 133, 244, 0.65);
  -moz-box-shadow: 0px 20px 35px -16px rgba(0, 133, 244, 0.65);
  box-shadow: 0px 20px 35px -16px rgba(0, 133, 244, 0.65);
}

.purple-btn {
  -webkit-box-shadow: 0px 20px 20px -17px rgba(126, 80, 202, 0.53);
  -moz-box-shadow: 0px 20px 20px -17px rgba(126, 80, 202, 0.53);
  box-shadow: 0px 20px 20px -17px rgba(126, 80, 202, 0.53);
}

.purple-btn:hover {
  -webkit-box-shadow: 0px 20px 35px -16px rgba(126, 80, 202, 0.65);
  -moz-box-shadow: 0px 20px 35px -16px rgba(126, 80, 202, 0.65);
  box-shadow: 0px 20px 35px -16px rgba(126, 80, 202, 0.65);
}

.darkblue-btn {
  -webkit-box-shadow: 0px 20px 20px -17px rgba(0, 73, 255, 0.53);
  -moz-box-shadow: 0px 20px 20px -17px rgba(0, 73, 255, 0.53);
  box-shadow: 0px 20px 20px -17px rgba(0, 73, 255, 0.53);
}

.darkblue-btn:hover {
  -webkit-box-shadow: 0px 20px 35px -16px rgba(0, 73, 255, 0.65);
  -moz-box-shadow: 0px 20px 35px -16px rgba(0, 73, 255, 0.65);
  box-shadow: 0px 20px 35px -16px rgba(0, 73, 255, 0.65);
}

.pink-btn {
  -webkit-box-shadow: 0px 20px 20px -17px rgba(227, 0, 158, 0.53);
  -moz-box-shadow: 0px 20px 20px -17px rgba(227, 0, 158, 0.53);
  box-shadow: 0px 20px 20px -17px rgba(227, 0, 158, 0.53);
}

.pink-btn:hover {
  -webkit-box-shadow: 0px 20px 35px -16px rgba(227, 0, 158, 0.65);
  -moz-box-shadow: 0px 20px 35px -16px rgba(227, 0, 158, 0.65);
  box-shadow: 0px 20px 35px -16px rgba(227, 0, 158, 0.65);
}

.projects {
  grid-row: 4;
  grid-column: 1 / span 2;
  margin: 0px 100px 50px 100px;
  padding: 0px 40px;
}

#subtext {
  margin: -10px 0 40px 18px;
}

h4 {
  font-size: 25px !important;
  /* font-family: "Rambla", sans-serif !important; */
  font-family: "Source Code Pro", monospace !important;
  /* text-align: left !important; */
  font-size: 40px !important;
}

.projectContainer {
  padding: 20px;
  border-radius: 20px;
  box-shadow: 2px 4px 25px rgba(0, 0, 0, 0.1);
  transition: all 0.2s linear;
  background: linear-gradient(
      338.83deg,
      #699df2 -3.41%,
      rgba(255, 196, 255, 0) 52.31%
    ),
    #a9f9ff;
  padding: 70px;
  margin-bottom: 50px;
  color: black;
}

.projectContainer:hover {
  box-shadow: 2px 8px 45px rgba(0, 0, 0, 0.15);
  transform: translate3D(0, -2px, 0);
}
